export default {

  //      
  calcPercent(purchase, price) {
    return ((price - purchase) * 100 / purchase).toFixed(2);
  },
  //      
  calcPrice(purchase, percent) {
    return (Number(purchase) + (purchase / 100 * percent)).toFixed(2);
  }
};



